@font-face {
  font-family: 'open_dyslexic';
  src: url('./fonts/OpenDyslexicAlta-Bold.otf') format('otf'),
       url('./fonts/OpenDyslexicAlta-BoldItalic.otf') format('otf'),
       url('./fonts/OpenDyslexicAlta-Italic.otf') format('otf'),
       url('./fonts/OpenDyslexicAlta-Regular.otf') format('otf'),
       url('./fonts/OpenDyslexic-Bold.otf') format('otf'),
       url('./fonts/OpenDyslexic-BoldItalic.otf') format('otf'),
       url('./fonts/OpenDyslexic-Italic.otf') format('otf'),
       url('./fonts/OpenDyslexicMono-Regular.otf') format('otf'),
       url('./fonts/OpenDyslexic-Regular.otf') format('otf');
} 
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
